	import Tabbar from '@/components/footer/Tabbar'
	import Header from '@/components/header/header'
	import Search from '@/components/header/Search/Search'
	import Vue from 'vue'

	import 'mint-ui/lib/style.css'
	import 'mint-ui/lib/index.js'

	import {getHotSearch} from '@/api/msite'
	import {getEntries} from '@/api/msite'
	import {getRestaurant} from '@/api/msite'
	import {Indicator} from 'mint-ui';
	import Star from '@/components/star/Star'
	import _ from 'lodash'	

	export default{
		components:{
			'ele-tabbar': Tabbar,
			'ele-header': Header,
			'ele-search': Search,
			'ele-star' : Star
		},
		data(){
			return{ 
				hotsearch: [],
				entries: [],
				restaurant: [],
				limit: 20,
				offset: 0,
				timer: null,
				isLoaded: false
			}
		},
		methods:{
			actClick(btnIndex){
				var btn = document.getElementsByClassName('activityBtn')[btnIndex];
				if(btn.classList.contains('activityBtn-active')){
					btn.classList.remove('activityBtn-active');
					btn.parentNode.classList.remove('shop-activity-active');

				}else{
					btn.classList.add('activityBtn-active');
					btn.parentNode.classList.add('shop-activity-active');
				}
			},
			scroll(){
				var scrollTop = document.body.scrollTop | document.documentElement.scrollTop;
				var scrollTop = scrollTop;
				var scrollY = document.body.scrollHeight | document.documentElement.scrollHeight;
				var devicHeight = window.innerHeight;
				if(scrollY == scrollTop + devicHeight){
					if(!this.isLoaded){
						this.isLoaded = true;
						getRestaurant(40.1144418,116.3579348,this.limit,this.offset += 21)
						.then((res) => {
							if(this.restaurant.length<=100){
								this.restaurant = this.restaurant.concat(res.data);
								this.isLoaded = false;
							}
							else{
								alert('你看不够吗?');
							}
						})
						.catch(() =>{
							console.log("商家数据拉取失败");
						})
					}
				}
			}
		},
		created(){
			// 监听滚动事件
			this.timer = setInterval(this.scroll,1000);
			// Indicator.close();
			Indicator.open({
			  text: '加载中...',
			  spinnerType: 'snake'
			});

			setTimeout(function(){
				Indicator.close();
			},10000);

			getHotSearch(40.1144418,116.3579348)
			.then((res) => {
				this.hotsearch = res.data;
			}).catch((res) =>{
				console.log('热搜数据拉取失败');
			})
			.then(() =>{
				getEntries(40.1144418,116.3579348)
				.then((res) => {
					this.entries = _.chunk(res.data[0].entries,8)
				}).catch((res) =>{
					console.log('分类列表拉取失败');
				})
			})
			.then(() => {
				getRestaurant(40.1144418,116.3579348,this.limit,this.offset)
				.then((res) => {
					this.restaurant = this.restaurant.concat(res.data);
				})
				.catch(() =>{
					console.log("商家数据拉取失败");
				})
			})
		},
		updated(){
			Indicator.close();
		},
		beforeDestroy(){
			clearInterval(this.timer);
		},
		filters:{
			imgUrl(str){
				let result = str.slice(0,1) + '/' + str.slice(1, 3) + '/' + str.slice(3);

				let name = result.match(/jpeg|jpg|png$/);
				return 'https://fuss10.elemecdn.com/'+result+'.'+name[0]+'?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/'
			}
		}
	}

	window.onscroll = function(){
		
	}



